import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { Router } from '@angular/router';

import { routerTransition } from './route-animations';
import { AuthService } from './services/auth/auth.service';
import { ApiService } from './services/api.service';
import { SessionService } from './services/data/session.service';
import { CustomerService } from './services/user/customer.service';
import { UserService } from './services/user/user.service';

import { environment } from '../environments/environment';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less'],
  animations: [routerTransition]
})
export class AppComponent implements OnInit {
  title = 'svd';
  pageTransition = '';
  isLoading = true;
  isDesktop:boolean = false;

  statusModal = {
    title: null,
    modalStatus: 'success',
    showStatusModal: false,
    content: 'Account created Successfully'
  };


  autheticationPromise = null;
  autheticationPromiseInProgress = false;

  constructor(
    private auth: AuthService
    , private router: Router
    , private api: ApiService
    , private session: SessionService
    , private customer: CustomerService
    , private userService: UserService
  ) {
    this.isLoading = false;
  }

  getState(outlet) {
    return this.pageTransition;
  }

  routerTransitionDone($event) {
    this.pageTransition = '';
  }

  navigateByUrl(url, pageTransition?) {
    this.pageTransition = pageTransition || 'slideleft';
    this.router.navigateByUrl(url);
  }

  setAccountType(roleName) {
    this.session.set('role', roleName);
  }


  showStatusModal(type, content, opts?) {
    this.statusModal.modalStatus = type;
    this.statusModal.content = content;
    if (opts && opts.title) {
      this.statusModal.title = opts.title;
    } else {
      this.statusModal.title = null;
    }
    this.statusModal.showStatusModal = true;
  }

  ngOnInit() {
    this.isDesktop = window.innerWidth <= 1024 ? false : true; 
  }

}



