import { Component, OnInit } from '@angular/core';

import { AppComponent } from '../../app.component';
import { AuxilaryService } from '../../services/auxilary.service';
import { SessionService } from '../../services/data/session.service';

@Component({
  selector: 'app-create-account-nameaddress',
  templateUrl: './create-account-nameaddress.component.html',
  styleUrls: ['./create-account-nameaddress.component.less']
})
export class CreateAccountNameaddressComponent implements OnInit {
  teamDetails = {
    firstName: '',
    lastName: '',
    phone: '',
    teamName: '',
    address: {
      region: '',
      country: 'USA',
      street1: '',
      street2: '',
      postcode: '',
      city: '',
      district: ''
    }
  };
  isTechnician = false;

  showStates = false;
  showCounty = false;
  StatesByCountry: Array<any> = [];
  States = [];
  Districts = [];
  constructor(
    public app: AppComponent,
    private aux: AuxilaryService,
    private session: SessionService
  ) { }

  getStates() {
    this.aux.getStates().then(({ err, resp }) => {
      if (!err && resp._embedded && resp._embedded.regions) {
        this.States = resp._embedded.regions;
        const states = resp._embedded.regions.filter((s) => {
          return s.country === this.teamDetails.address.country;
        });
        this.StatesByCountry = states;
      }
    });
  }

  setState(state) {
    this.teamDetails.address.region = state.abbreviation;
    this.aux.getDistricts({
      country: this.teamDetails.address.country,
      region: this.teamDetails.address.region
    }).then(({ err, resp }) => {
      if (!err && resp._embedded && resp._embedded.serviceAreas) {
        this.Districts = resp._embedded.serviceAreas;
      }
    });
  }

  setCounty(county) {
    this.teamDetails.address.district = county.description;
  }

  getProvider(user) {
    this.aux.getCustomersByOwnerEmail({
      owneremail: user.email
    }).then(({ err, resp }) => {

    });
  }

  ngOnInit() {
    this.isTechnician = false;
    const user = this.session.get('user');
    if (user) {
      this.getStates();
    } else {
      this.aux.authenticate().then(({ err, resp }) => {
        const user = resp;
        this.getStates();
      });
    }
  }

  updateCountry(selectedCountry) {
    const states = this.States.filter((s) => {
      return s.country === selectedCountry;
    });
    this.StatesByCountry = states;
    this.teamDetails.address.region = '';
    this.teamDetails.address.district = '';
  }

  updateUser(user) {
    return new Promise((r, j) => {
      if (this.isTechnician) {
        const roles = user.roles;
        if (roles.filter((r) => { return r.role === 'TECHNICIAN'; }).length === 0) {
          user.roles.push({
            role: 'TECHNICIAN'
          });
        }
        this.aux.updateUser({
          id: user.id,
          firstName: this.teamDetails.firstName,
          lastName: this.teamDetails.lastName,
          phone: this.teamDetails.phone,
          roles: user.roles
        }).then(({ err, resp }) => {
          r({ err, resp });
        });
      } else {
        this.aux.updateUser({
          id: user.id,
          firstName: this.teamDetails.firstName,
          lastName: this.teamDetails.lastName,
          phone: this.teamDetails.phone
        }).then(({ err, resp }) => {
          r({ err, resp });
        });
      }
    });
  }

  createProviderTeam() {
    const user = this.session.get('user');
    if (user) {
      this.updateUser(user).then(({ err, resp }) => {
        if (!err) {
          this.session.set('user', resp);
        }
        this.aux.addProvider({
          name: this.teamDetails.teamName,
          address: this.teamDetails.address
        }).then(({ err, resp }) => {
          if (!err) {
            this.session.set('provider', resp);
            this.app.navigateByUrl('/company-details');
          }
        });
      });
    }
  }
}



