import { Component, OnInit } from '@angular/core';

import { AppComponent } from '../../app.component';
import { AuxilaryService } from '../../services/auxilary.service';
import { SessionService } from '../../services/data/session.service';

@Component({
  selector: 'app-address-list',
  templateUrl: './address-list.component.html',
  styleUrls: ['./address-list.component.less']
})
export class AddressListComponent implements OnInit {

  Addresses = [];
  filterAddresses = [];
  searchTerm;
  isMachine = false;
  constructor(
    public app: AppComponent,
    private aux: AuxilaryService,
    private session: SessionService
  ) { }

  getAddresses(user) {
    this.aux.getCustomersByOwnerEmail({
      owneremail: user.email
    }).then(({ err, resp }) => {
      if (!err) {
        this.session.set('customer', resp);
      }
      if (resp.addresses) {
        this.Addresses = resp.addresses;
      } else {
        this.Addresses = [];
      }
      this.getFilterAddress();
    });
  }

  getFilterAddress(control?) {
    let termToSearch = null;
    if (control) {
      termToSearch = control.value;
    }
    if (!termToSearch) {
      this.filterAddresses = this.Addresses;
    } else {
      this.filterAddresses = this.Addresses.filter((a) => {
        let found = false;
        if (a['companyName'] && a['companyName'].toLowerCase().indexOf(termToSearch.toLowerCase()) === 0) {
          found = true;
        }
        if (a['street1'] && a['street1'].toLowerCase().indexOf(termToSearch.toLowerCase()) === 0) {
          found = true;
        }
        if (a['street2'] && a['street2'].toLowerCase().indexOf(termToSearch.toLowerCase()) === 0) {
          found = true;
        }
        return found;
      });
    }
  }
  ngOnInit() {
    const user = this.session.get('user');
    if (user) {
      const customer = this.session.get('customer');
      if (customer) {
        this.Addresses = customer.addresses;
        this.getFilterAddress();
      } else {
        this.getAddresses(user);
      }

    } else {
      this.aux.authenticate().then(({ err, resp }) => {
        if (!err) {
          this.session.set('user', resp);
          this.getAddresses(resp);
        }
      });
    }

    if (this.session.get('isMachine')) {
      this.isMachine = true;
    }

    this.session.set('editAddress', null);
  }

  addAdrress() {
    this.app.navigateByUrl('/address-new');
  }

  goToMachines() {
    this.app.navigateByUrl('/machines-list');
  }

  selectAddress(address) {
    if (this.session.get('isMachine') && this.session.get('back_url')) {
      this.session.set('isMachine', null);
      this.session.set('selectedAddress', address);
      this.app.navigateByUrl(this.session.get('back_url'));
    }
  }

  editAddress(event, address) {
    event.stopPropagation();
    this.session.set('editAddress', address);
    this.app.navigateByUrl('/address-edit/' + address.id);
  }

}



