import { Injectable } from '@angular/core';
import { ApiService, Opts } from '../api.service';
import { User } from '../../models/user';

@Injectable()
export class UserService {

  constructor(
    private api: ApiService
  ) { }

  createUser(opts) {
    let headers = {};
    if (opts.headers) {
      headers = opts.headers;
    }
    const formData = {
      email: opts.email,
      password: opts.password,
      roles: opts.roles
    };
    if (opts.firstName) {
      formData['firstName'] = opts.firstName;
    }
    if (opts.firstName) {
      formData['lastName'] = opts.lastName;
    }
    if (opts.firstName) {
      formData['phone'] = opts.phone;
    }
    if (opts.safetyTrained) {
      formData['safetyTrained'] = opts.safetyTrained;
    }
    return this.api.post({
      url: '/users',
      data: formData,
      headers: headers
    });
  }

  getUsers() {
    return this.api.post({
      url: '/auth/login',
      data: {
        email: 'mtottimpudi@evoketechnologies.com',
        password: 'password'
      }
    }).then(({ err, fullResponse }) => {
      const Authorization = fullResponse.getResponseHeader('authorization');
      return this.api.get({
        url: '/users',
        headers: {
          Authorization: Authorization
        }
      });
    });
  }

  resendVerificationCode(opts) {
    let headers = {};
    if (opts.headers) {
      headers = opts.headers;
    }
    return this.api.post({
      url: '/users/' + opts.id + '/actions/reactivate',
      headers: headers
    });
  }

  verifyUser(opts) {
    let headers = {};
    if (opts.headers) {
      headers = opts.headers;
    }
    return this.api.post({
      url: '/auth/activate',
      data: {
        email: opts.email,
        authToken: opts.authToken
      },
      headers: headers
    });
  }

  updateUser(opts) {
    if (opts.id) {
      const updatingUser: any = {};
      updatingUser.id = opts.id;
      if (opts.firstName) {
        updatingUser.firstName = opts.firstName;
      }
      if (opts.lastName) {
        updatingUser.lastName = opts.lastName;
      }
      if (opts.phone) {
        updatingUser.phone = opts.phone;
      }

      if (opts.roles) {
        updatingUser.roles = opts.roles;
      }

      if (opts.safetyTrained != null) {
        updatingUser.safetyTrained = opts.safetyTrained;
      }
      return this.api.patch({
        url: '/users/' + opts.id,
        data: updatingUser
      });
    } else {
      return new Promise((r, e) => {
        r({
          err: {
            code: 400,
            msg: 'user id is required'
          }
        });
      });
    }
  }

  updateUserManufacturers(opts) {
    if (opts.id) {
      return this.api.post({
        url: '/users/' + opts.id + '/manufacturers',
        data: { manufacturers: opts.manufacturers }
      });
    } else {
      return new Promise((r, e) => {
        r({
          err: {
            code: 400,
            msg: 'user id is required'
          }
        });
      });
    }
  }

  updateUserTechnologyTypes(opts) {
    if (opts.id) {
      return this.api.post({
        url: '/users/' + opts.id + '/technologyTypes',
        data: { technologyTypes: opts.technologyTypes }
      });
    } else {
      return new Promise((r, e) => {
        r({
          err: {
            code: 400,
            msg: 'user id is required'
          }
        });
      });
    }
  }


}



