import { Component, OnInit } from '@angular/core';

import { AppComponent } from '../../app.component';
import { AuxilaryService } from '../../services/auxilary.service';
import { SessionService } from '../../services/data/session.service';

@Component({
  selector: 'app-service-areas',
  templateUrl: './service-areas.component.html',
  styleUrls: ['./service-areas.component.less']
})
export class ServiceAreasComponent implements OnInit {
  States = [];
  Regions = [];

  filterStates = [];
  filterRegions = [];
  Districts = [];
  DistrictsHash = {};

  isStates = true;
  showCounty = false;
  selectedServiceAreas = [];

  constructor(
    public app: AppComponent,
    private aux: AuxilaryService,
    private session: SessionService
  ) { }


  getProvider(user) {
    this.aux.getProviderByOwnerEmail({
      owneremail: user.email
    }).then(({ err, resp }) => {
      if (!err) {
        this.session.set('provider', resp);
      }
    });
    this.getStates();
  }

  getStates() {
    this.aux.getStates().then(({ err, resp }) => {
      if (!err && resp._embedded && resp._embedded.regions) {
        const list = resp._embedded.regions;
        let states = [], regions = [];
        list.forEach((r) => {
          if (r.country === 'USA') {
            states.push(r);
          } else {
            regions.push(r);
          }
        });
        this.Regions = regions;
        this.States = states;

        this.filterRegions = regions;
        this.filterStates = states;
      }
    });
  }
  ngOnInit() {
    const user = this.session.get('user');
    if (user) {
      this.getProvider(user);
    } else {
      this.aux.authenticate().then(({ err, resp }) => {
        this.getProvider(resp);
      });
    }
  }

  setServiceArea() {
    this.session.set('selectedServiceAreas', this.selectedServiceAreas);
    this.app.navigateByUrl('/company-details');
  }

  toggleServiceArea(item) {
    item.selected = !item.selected;
    this.Districts = [];
    this.getDistricts(item);
    this.showCounty = true;
  }

  getDistricts(item) {
    if (this.DistrictsHash[item.country] && this.DistrictsHash[item.country][item.abbreviation]) {
      this.Districts = this.DistrictsHash[item.country][item.abbreviation];
    } else {
      this.aux.getDistricts({
        country: item.country,
        region: item.abbreviation
      }).then(({ err, resp }) => {
        if (!err && resp._embedded && resp._embedded.serviceAreas) {
          this.Districts = resp._embedded.serviceAreas;
          if (!this.DistrictsHash[item.country]) {
            this.DistrictsHash[item.country] = {};
          }
          this.DistrictsHash[item.country][item.abbreviation] = resp._embedded.serviceAreas;
        }
      });
    }
  }
  getFilterResults(control?) {
    let searchTerm = null;
    if (control) {
      searchTerm = control.value;
      if (searchTerm && searchTerm.length > 0) {
        searchTerm = searchTerm.toLowerCase();
        this.filterRegions = this.Regions.filter(r => r.description.toLowerCase().indexOf(searchTerm) === 0);
        this.filterStates = this.States.filter(r => r.description.toLowerCase().indexOf(searchTerm) === 0);
      } else {
        this.filterRegions = this.Regions;
        this.filterStates = this.States
      }
    } else {
      this.filterRegions = this.Regions;
      this.filterStates = this.States
    }
  }


  setCounty(item) {
    var items = [], itemAdded = false;
    this.selectedServiceAreas.forEach((s) => {
      if (item.id === s.id) {
        itemAdded = true;
      } else {
        items.push(s);
      }
    });
    if (!itemAdded) {
      items.push(item);
    }
    this.selectedServiceAreas = items;
  }
  isSelected(item, prop1, prop2) {
    return this.selectedServiceAreas.filter(s => {
      if (s && item && s[prop1] === item[prop2]) {
        return true;
      } else {
        return false;
      }
    }).length > 0;
  }

}



