import { Component, OnInit } from '@angular/core';
import { AppComponent } from '../../app.component';

import { AuxilaryService } from '../../services/auxilary.service';
import { SessionService } from '../../services/data/session.service';
@Component({
  selector: 'app-my-machines',
  templateUrl: './my-machines.component.html',
  styleUrls: ['./my-machines.component.less']
})
export class MyMachinesComponent implements OnInit {
  Machines = [];
  filterMachines = [];
  constructor(
    public app: AppComponent,
    private session: SessionService,
    private aux: AuxilaryService
  ) { }

  getMachines(customer) {
    this.aux.getMachinesByCustomer({
      customerid: customer.id
    }).then(({ err, resp }) => {
      if (!err && resp._embedded && resp._embedded.machines) {
        this.Machines = resp._embedded.machines;
      } else {
        this.Machines = [];
      }
      this.getFilterMachines();
    });
  }

  getFilterMachines(control?) {
    let termToSearch = null;
    if (control) {
      termToSearch = control.value;
    }
    if (!termToSearch) {
      this.filterMachines = this.Machines;
    } else {
      this.filterMachines = this.Machines.filter((a) => {
        let found = false;
        if (a['manufacturer'] && a['manufacturer'].name && a['manufacturer'].name.toLowerCase().indexOf(termToSearch.toLowerCase()) === 0) {
          found = true;
        }
        if (a['modelNumber'] && a['modelNumber'].toLowerCase().indexOf(termToSearch.toLowerCase()) === 0) {
          found = true;
        }
        if (a['serialNumber'] && a['serialNumber'].toLowerCase().indexOf(termToSearch.toLowerCase()) === 0) {
          found = true;
        }
        return found;
      });
    }
  }

  getCustomer(user) {
    this.aux.getCustomersByOwnerEmail({
      owneremail: user.email
    }).then(({ err, resp }) => {
      this.getMachines(resp);
    });
  }
  ngOnInit() {
    const customer = this.session.get('customer');
    if (customer) {
      this.getMachines(customer);
    } else {
      const user = this.session.get('user');
      if (user) {
        this.getCustomer(user);
      } else {
        this.aux.authenticate().then(({ err, resp }) => {
          if (!err) {
            this.getCustomer(resp);
          }
        });
      }
    }
  }

  addMachine() {
    this.app.navigateByUrl('/machines-new');
  }

  editMachine(event, machine) {
    event.stopPropagation();
    this.session.set('editMachine', machine);
    this.app.navigateByUrl('/machines-edit/' + machine.id);
  }

}



