import { Component, OnInit } from '@angular/core';

import { AppComponent } from '../../app.component';

import { AuxilaryService } from '../../services/auxilary.service';
import { SessionService } from '../../services/data/session.service';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.less']
})
export class ResetPasswordComponent implements OnInit {
  emailToReset = 'muralikrishna8811+cp1509751073509@gmail.com';
  constructor(
    public app: AppComponent,
    private session: SessionService,
    private aux: AuxilaryService
  ) { }

  ngOnInit() {
  }

  resetPasswordEmail() {
    this.aux.resetPassword({
      email: this.emailToReset
    });
  }
}



